/* jshint ignore:start */

define('rastopyr-github/config/environment', ['ember'], function(Ember) {
  var prefix = 'rastopyr-github';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("rastopyr-github/tests/test-helper");
} else {
  require("rastopyr-github/app")["default"].create({"name":"rastopyr-github","version":"0.0.0.a20b3986"});
}

/* jshint ignore:end */
